<template>
  <div class="monitor">
    <el-container>
      <el-header height="30px">
        <div class="title" style="float: left; font-size: 30px; font-weight: 700">
          采集表填写记录
        </div>
      </el-header>
      <el-divider/>
      <el-main>
        <div class="analyzeTables" :model="monitorTable">
          <el-table :data="answers" max-height="600px" stripe="">
            <el-table-column property="respondent" label="填写人" width="200"/>
            <el-table-column property="date" label="填写时间" width="200"/>
            <el-table-column label="" width="180">  
              <template v-slot:default="scope">  
                <el-button @click="seeDetails(scope.row)">查看详细信息</el-button>  
              </template>  
            </el-table-column>
          </el-table>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  name: 'Monitor',
  data() {
    return {
      answerList: [
        {
          respondent: "test",
          date: "2020-01-12"
        }
      ],
      showResetModal: false,
      // datepicker: false,
      answersVisible: false,
      radio: '1',
      date: ''
    }
  },
  async mounted() {
    const tableId = this.$route.params.tableId
    //let res = await this.getFullTableStatistic(tableId)
    //if (!res) {
    //  this.$message.error('采集表统计加载失败，请询问管理员')
    //  return
    //}
    let res = await this.getAnswersRow(tableId)
    console.log("answers: ", this.answers)
  },
  computed: {
    ...mapGetters(['monitorTable', 'answers']),
    datepicker() {
      return this.radio === '2'
    },
  },
  methods: {
    ...mapActions([
      'getFullTableStatistic',
      'terminateCollection',
      'restartCollection',
      'getAnswersRow'
    ]),
    seeDetails(row) {  
      console.log('你点击了', row);  
      // 在这里处理点击事件，比如跳转到详情页等 
      this.$router.push({
        name: 'collection',
        params: {
          tableId: row.tableId,
          answerId: row.answerId
        } 
      }) 
    },
    endCollection() {
      this.$confirm('确定结束此采集表收集？', '结束收集', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.terminateCollection(this.monitorTable).then((res) => {
            this.$message({
              type: 'warning',
              message: '采集表收集结束！'
            })
          })
        })
        .catch(() => {})
    },
    resetCollection() {
      const date = this.datepicker ? this.date : null
      console.log(`date: ${date}`)
      this.restartCollection(date).then((res) => {
        if (res) {
          this.$message.success('重新发放成功')
          console.log(this.monitorTable)
        } else {
          this.$message.error('发放失败')
        }
      })
      this.showResetModal = false
    }
  }
}
</script>

